.wrapper {
  background-color: $light-color;

  &--first-screen {
    background-color: $navy-blue;
    border-bottom-left-radius: 200px;
  }

  &--expertise {
    background-color: #fff;
  }

  &--approach {
    background-color: $pink;
    border-top-left-radius: 200px;
    border-bottom-left-radius: 200px;
  }

  &--footer {
    background-color: $navy-blue;
    border-top-right-radius: 200px;
  }
}
